body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: powderblue;
}

.terminal {
  height: 22.5rem;
  width: 45rem;
  border-radius: 5px;
  background-image: linear-gradient(gray 0, gray 1.8rem, black 1.8rem, black 100%);
}
.terminal::before {
  content: "";
  position: absolute;
  background-color: #BF616A;
  width: 1.44rem;
  aspect-ratio: 1;
  border-radius: 100%;
  margin-top: 0.288rem;
  margin-left: 0.5%;
  box-shadow: 2.16rem 0px 0px #EBCB8B, 4.32rem 0px 0px #A3BE8C;
}
.terminal::after {
  content: "";
  position: absolute;
  width: 9rem;
  aspect-ratio: 1;
  margin-top: 7.5rem;
  border-left: 2.25rem solid white;
  border-bottom: 2.25rem solid white;
  transform: rotate(225deg);
}